@mixin buttonTransform {
  transition: color 1s, border 1s, background-color 1s;
}

@mixin linkTransform {
  transition: color 1s;
}

@mixin h1 {
  font-size: 36px;
  font-weight: 600;
  line-height: 42px;
}

@mixin h2 {
  font-size: 28px;
  line-height: 33px;
}

@mixin h3 {
  font-size: 20px;
  line-height: 23px;
}

@mixin indentReset {
  margin: 0;
  padding: 0;
}

@mixin fontStyleDefault {
  font-family: "Avenir", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #3d4e61;
}

@mixin fontStyleSmall {
  font-family: "Avenir", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #3d4e61;
}

@mixin link {
  font-weight: 900;
  color: #3d4e61;
  text-decoration: none;

  &:hover {
    color: #118488;
    text-decoration: underline;
  }
}
